import { useDispatch, useSelector } from 'react-redux';

import { useState } from 'react';
import Notiflix from 'notiflix';

import { addContact } from 'redux/contacts/contacts.slice';
import { getContacts } from 'redux/contacts/contacts.selector';
import {
  Button,
  Col,
  Container,
  FloatingLabel,
  Form,
  Row,
} from 'react-bootstrap';

export default function ContactForm() {
  const [name, setName] = useState('');
  const [number, setNumber] = useState('');

  const dispatch = useDispatch();
  const contacts = useSelector(getContacts);

  const handleSubmit = event => {
    event.preventDefault();

    if (!contacts.find(contact => contact.name.includes(name))) {
      dispatch(addContact({ name: name, number: number }));

      Notiflix.Notify.success(name + ' added in contacts');
    } else {
      const message = ' is already in contacts';
      Notiflix.Notify.failure(name + message);
    }
    setName('');
    setNumber('');
  };

  const handleChange = event => {
    switch (event.target.name) {
      case 'name':
        setName(event.target.value);
        break;
      case 'number':
        setNumber(event.target.value);
        break;
      default:
        break;
    }
  };

  return (
    <Container>
      <Form onSubmit={handleSubmit}>
        <Row className="g-2 mb-2">
          <Col md>
            <FloatingLabel controlId="floatingInputGrid" label="Nanme">
              <Form.Control
                onChange={handleChange}
                type="text"
                name="name"
                value={name}
                pattern="^[a-zA-Z--]+(([' -][a-zA-Z-- ])?[a-zA-Z--]*)*$"
                title="Name may contain only letters, apostrophe, dash and spaces. For example Adrian, Jacob Mercer, Charles de Batz de Castelmore d'Artagnan"
                required
                placeholder="Enter name"
              />
            </FloatingLabel>
          </Col>
          <Col md>
            <FloatingLabel controlId="floatingInputGrid" label="Number">
              <Form.Control
                onChange={handleChange}
                value={number}
                type="tel"
                name="number"
                pattern="\+?\d{1,4}?[-.\s]?\(?\d{1,3}?\)?[-.\s]?\d{1,4}[-.\s]?\d{1,4}[-.\s]?\d{1,9}"
                title="Phone number must be digits and can contain spaces, dashes, parentheses and can start with +"
                required
                placeholder="Enter number"
              />
            </FloatingLabel>
          </Col>
        </Row>
        <Button variant="primary" type="submit">
          Add contact
        </Button>
      </Form>
    </Container>
  );
}
